import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate';
Vue.use(Vuex)

/* state */
const state = {
    title: 'a-biu'
};


/* state() */
const getters = {
    /* ,changableNum */
    getTitle(state) {
        return state.title
    }

};
/* state */
const mutations = {
    /* statesum */
    newTitle(state, text) {
        state.title = text;
    }
};
/* mutations */
const actions = {
    /* num */
    getNewTitle(context, text) {
        context.commit('newTitle', text)
    }
};

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
    plugins: [createPersistedState({
        // storage: window.sessionStorage,
        // reducer(val) {
        //     return {
        //         // stateassessmentData
        //         changableNum: val.changableNum
        //     }
        // }
    })]
})